import React from 'react';
import '../css/Weather.css';
import Timestamp from 'react-timestamp';

export default class Weather extends React.Component{
    
    constructor(props){
        super(props);
        this.state = {
            temperature: undefined,
            city: undefined,
            country: undefined,
            humidity: undefined,
            description: undefined,
            icon: undefined,
            sunrise: undefined,
            sunset: undefined,
            temp_min: undefined,
            temp_max: undefined
        };
    }
    
    componentDidMount() {
        fetch(`https://api.openweathermap.org/data/2.5/weather?q=copenhagen,dk&APPID=4d9346c553e2e7240e772ef6327a5718&units=metric`)
            .then(response => response.json())
            .then(result => this.setState({
                temperature: result.main.temp,
                city: result.name,
                country: result.sys.country,
                humidity: result.main.humidity,
                description: result.weather[0].description.toUpperCase(),
                icon: result.weather[0].icon,
                sunrise: result.sys.sunrise,
                sunset: result.sys.sunset,
                temp_min: result.main.temp_min,
                temp_max: result.main.temp_max
            }));
    }

    render(){
        // console.log(this.state.weather);
        let icon = this.state.icon;

        return(
            <div className="weatherContainer">
                <div className="weatherInner">
                <h1>Current Weather</h1>
                    <div className="innerGrid">
                        <div className="leftBox">
                            <h2 className="weatherCity">{this.state.city}, {this.state.country}</h2>
                            <p><Timestamp date={this.state.dt} /></p>
                            <img className="weatherIcon" src={`https://openweathermap.org/img/wn/${icon}@2x.png`}/>
                            <h2>{this.state.temperature} °C</h2>
                            <h3>{this.state.description}</h3>
                        </div>
                        <div className="rightBox">
                            <h2>Details</h2>
                            <p>Humidity: {this.state.humidity} %</p>
                            <p>Min temp: {this.state.temp_min} °C</p>
                            <p>Max temp: {this.state.temp_max} °C</p>

                            <p>Sunrise: <Timestamp date={this.state.sunrise} /></p>
                            <p>Sunset: <Timestamp date={this.state.sunset} /></p>
                           
                        </div>
                    </div>
                </div>
            </div>
        );
    };
}
