import React from 'react';
import Timestamp from 'react-timestamp';
import '../css/Forecast.css';

export default class Forecast extends React.Component{

    constructor(props){
        super(props);
        this.state = {
           dataList: [],
           city: "",
           country: "",
           error: null,
           isLoaded: false
        };
    }
    
    componentDidMount() {
        fetch(`https://api.openweathermap.org/data/2.5/forecast?q=copenhagen,dk&APPID=4d9346c553e2e7240e772ef6327a5718&units=metric`)
            .then(response => response.json())
            .then(
                (finalResult) => {
                    this.setState({
                        dataList: finalResult.list,
                        city: finalResult.city.name,
                        country: finalResult.city.country,
                        isLoaded: true
                    });
        },
        (error) => {
            this.setState({
              isLoaded: true,
              error
            });
          }
        )
    }

    render(){
        // console.log(this.state.dataList);
        const { error, isLoaded } = this.state;
        if (error) {
          return <div>Error: {error.message}</div>;
        } else if (!isLoaded) {
          return <div>Loading...</div>;
        } else {

        return(
            <div className="forecastContainer">
                <h1>Weather Forecast</h1>                

               <div className="forecastInner">

                    {this.state.dataList.map(data =>{
                        return(
                        <div className="forecastBox">
                                <h3>{this.state.city}</h3>
                                <p><Timestamp date={data.dt}/></p>
                                <h2>{data.main.temp} °C</h2>

                                {data.weather.map(weatherData =>{
                                    let description = weatherData.description.toUpperCase();
                                    return(
                                        <div>
                                            {/* <p>{weatherData.main}</p> */}
                                            <h4>{description}</h4>
                                            <img src={`https://openweathermap.org/img/wn/${weatherData.icon}@2x.png`}/>
                                        </div>
                                    )
                                })}                                
                                <p>Min temp: {data.main.temp_min} °C</p>
                                <p>Max temp: {data.main.temp_max} °C</p>
                                <p>Humidity: {data.main.humidity} %</p>
                        </div>
                        )
                    })}
                </div>
            </div>
            );
        };
    }
}

